import React, { useCallback, useContext, useEffect, useState } from "react";
import TextField from "@mui/material/TextField";
import AddCommentIcon from "@mui/icons-material/AddComment";
import FavoriteIcon from "@mui/icons-material/Favorite";
import ShareIcon from "@mui/icons-material/Share";
import DeleteIcon from "@mui/icons-material/Delete";
import ReplyIcon from "@mui/icons-material/Reply";
import "./Post.css";
import axios from "axios";
import { baseUrl } from "../../utils/constants";

import { MyContext } from "../../Context/AppContext";

const Post = ({ showToast }) => {
  const { user } = useContext(MyContext);

  const [posts, setPosts] = useState([]);
  const [comment, setComment] = useState("");
  const [comments, setComments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activePost, setActivePost] = useState(null);
  const [replyComment, setReplyComment] = useState("");
  const [showCommentField, setShowCommentField] = useState(false);
  const [showReplyCommentField, setShowReplyCommentField] = useState(null);
  const token = localStorage.getItem("token");

  const handleCommentChange = (event) => {
    setComment(event.target.value);
  };

  const handleCommentButtonClick = (postId) => {
    setShowCommentField(true);
    setActivePost(postId); // Set the active post for commenting
  };

  const handleCommentSubmit = async (event) => {
    event.preventDefault();
    if (!token) {
      showToast("Please login to add a comment", "info");
      return;
    }
    try {
      const { data } = await axios.post(
        `${baseUrl}/addComment`,
        {
          text: comment,
          parentComment: null,
          postId: activePost, // Use the active post for commenting
        },
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );
      setComments([...comments, data.data]);
      setComment("");
      showToast(data?.message, "success");
      setShowCommentField(false);
    } catch (error) {
      const errorMessage =
        error?.response?.data?.message || "An error occurred";
      showToast(errorMessage, "error");
    }
  };

  const handleReplyComment = (postId, commentId) => {
    setShowReplyCommentField(commentId);
  };

  const handleReplySubmit = async (event, postId, commentId) => {
    event.preventDefault();
    if (!token) {
      showToast("Please login to reply to a comment", "info");
      return;
    }
    try {
      const { data } = await axios.post(
        `${baseUrl}/addComment`,
        {
          text: replyComment,
          parentComment: commentId,
          postId: postId,
        },
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );
      const updatedComments = comments.map((comment) => {
        if (comment._id === commentId) {
          return {
            ...comment,
            replies: [...(comment.replyComments || []), data.data],
          };
        }
        return comment;
      });
      setComments(updatedComments);
      setReplyComment("");
      showToast(data?.message, "success");
      setShowReplyCommentField(null);
    } catch (error) {
      const errorMessage =
        error?.response?.data?.message || "An error occurred";
      showToast(errorMessage, "error");
    }
  };

  const handleCommentLike = async (commentId) => {
    if (!token) {
      showToast("Please login to like a comment", "info");
      return;
    }

    try {
      const response = await axios.post(
        `${baseUrl}/likeComment/${commentId}`,
        null,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );
      const { success, message, data } = response.data;

      if (success) {
        // Update the comment in your state or perform any necessary actions
        const updatedComments = comments.map((comment) => {
          if (comment._id === commentId) {
            return { ...comment, liked: !comment.liked, likes: data.likes };
          }
          return comment;
        });
        setComments(updatedComments);
        showToast(message, "success");

        // Save the updated comments to local storage
        localStorage.setItem("Likecomments", JSON.stringify(updatedComments));
      } else {
        // Handle error cases
        showToast(message, "error");
      }
    } catch (error) {
      // Handle network errors or other exceptions
      console.error("An error occurred:", error);
    }
  };

  const handleReplyLike = async (replyId) => {
    if (!token) {
      showToast("Please login to like a reply", "info");
      return;
    }

    try {
      const response = await axios.post(
        `${baseUrl}/likeComment/${replyId}`,
        null,
        {
          headers: { Authorization: `Bearer ${token}` },
        }
      );
      const { success, message, data } = response.data;

      if (success) {
        // Update the reply in your state or perform any necessary actions
        const updatedReplyComments = comments.map((comment) => {
          if (comment._id === replyId) {
            const updatedReplies = comment.replyComments.map((reply) => {
              if (reply._id === replyId) {
                return { ...reply, liked: !reply.liked, likes: data.likes };
              }
              return reply;
            });
            return { ...comment, replyComments: updatedReplies };
          }
          return comment;
        });
        setComments(updatedReplyComments);
        showToast(message, "success");

        // Save the updated reply comments to local storage
        localStorage.setItem(
          "LikeReplyComments",
          JSON.stringify(updatedReplyComments)
        );
      } else {
        // Handle error cases
        showToast(message, "error");
      }
    } catch (error) {
      // Handle network errors or other exceptions
      console.error("An error occurred:", error);
    }
  };

  const handleLike = async (postId) => {
    if (!token) {
      showToast("Please login to like the post", "info");
      return;
    }
    try {
      const { data } = await axios.post(
        `${baseUrl}/like/${postId}`,
        {},
        { headers: { Authorization: `Bearer ${token}` } }
      );
      const updatedPosts = posts.map((post) => {
        if (post._id === postId) {
          return { ...post, likeCount: data.likeCount };
        }
        return post;
      });
      setPosts(updatedPosts);
      showToast(data?.message, "success");
      // Save the liked state to local storage
      localStorage.setItem("likedPosts", JSON.stringify(updatedPosts));
    } catch (error) {
      const errorMessage =
        error?.response?.data?.message || "An error occurred";
      showToast(errorMessage, "error");
    }
  };

  const handleDeleteComment = async (postId, commentId) => {
    if (!token) {
      showToast("Please login to delete the comment", "info");
      return;
    }
    try {
      const { data } = await axios.delete(`${baseUrl}/delete/${commentId}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      const updatedComments = comments.filter(
        (comment) => comment._id !== commentId
      );
      setComments(updatedComments);
      showToast(data?.message, "success");
    } catch (error) {
      const errorMessage =
        error?.response?.data?.message || "An error occurred";
      console.log(error);
    }
  };
  const handleDeleteReply = async (postId, commentId, replyId) => {
    if (!token) {
      showToast("Please login to delete the reply", "info");
      return;
    }
    try {
      const { data } = await axios.delete(`${baseUrl}/delete/${replyId}`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      const updatedComments = comments.map((comment) => {
        if (comment._id === commentId) {
          const updatedReplies = comment.replyComments.filter(
            (reply) => reply._id !== replyId
          );
          return { ...comment, replies: updatedReplies };
        }
        return comment;
      });
      setComments(updatedComments);
      showToast(data?.message, "success");
    } catch (error) {
      const errorMessage =
        error?.response?.data?.message || "An error occurred";
      showToast(errorMessage, "error");
    }
  };

  const isPostLiked = (postId) => {
    if (token) {
      const likedPost = posts.find((post) => post._id === postId);

      return likedPost && likedPost?.likes?.includes(user?._id);
    }
    return false;
  };
  const isCommentLiked = (commentId) => {
    if (token) {
      const likedComment = comments.find(
        (comment) => comment._id === commentId
      );

      return likedComment && likedComment?.likes?.includes(user?._id);
    }
    return false;
  };
  // useEffect(() => {
  //   const getAllPosts = async () => {
  //     try {
  //       const { data } = await axios.get(`${baseUrl}/getAllPosts`);

  //       const postsWithLikeCount = data.map((post) => ({
  //         ...post,
  //         likeCount: post.likes.length,
  //         liked: false, // Add the liked property to track the liked state
  //       }));
  //       setPosts(postsWithLikeCount);
  //       setLoading(false);
  //     } catch (error) {
  //       const errorMessage =
  //         error?.response?.data?.message || "An error occurred";
  //       showToast(errorMessage, "error");
  //       setLoading(false);
  //     }
  //   };

  //   getAllPosts();
  // }, [setComments, setPosts, isPostLiked, isCommentLiked, showToast]);
  const getAllPosts = useCallback(async () => {
    try {
      const { data } = await axios.get(`${baseUrl}/getAllPosts`);

      const postsWithLikeCount = data.map((post) => ({
        ...post,
        likeCount: post.likes.length,
        liked: false, // Add the liked property to track the liked state
      }));
      setPosts(postsWithLikeCount);
      setLoading(false);
    } catch (error) {
      const errorMessage =
        error?.response?.data?.message || "An error occurred";
      showToast(errorMessage, "error");
      setLoading(false);
    }
  }, [setPosts, showToast]);

  useEffect(() => {
    getAllPosts();
  }, [getAllPosts]);
  return (
    <div className="image middle">
      {posts.map((post, i) => (
        <div key={i} className="postDiv">
          <h1 className="image-heading">{post?.text}</h1>
          <img src={`${baseUrl}/${post?.file}`} alt={post?.file} />
          <div className="image-content">
            <div className="icons">
              <span>{post.likeCount}</span>
              <FavoriteIcon
                className={`icon ${isPostLiked(post._id) ? "liked" : ""}`}
                onClick={() => handleLike(post._id)}
              />
              <AddCommentIcon
                className="icon"
                onClick={() => handleCommentButtonClick(post._id)} // Pass the post ID to the click handler
              />
              <span>{post?.comments.length}</span>
              <ShareIcon className="icon" />
            </div>
            {showCommentField && activePost === post._id && (
              <form onSubmit={handleCommentSubmit} className="comment-form">
                <TextField
                  id="comment-input"
                  label="Add a comment"
                  variant="outlined"
                  value={comment}
                  onChange={handleCommentChange}
                />
                <button type="submit" className="comment-button">
                  Post
                </button>
              </form>
            )}
            {post.comments.map((comment, index) => (
              <div key={index} className="comment">
                <div
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    flexDirection: "row",
                  }}
                >
                  <div className="comment-content">
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        height: "40px",
                        width: "40px",
                        borderRadius: "50%",
                        border: "1px solid black",
                        marginRight: "5px",
                      }}
                    >
                      <img
                        style={{
                          height: "40px",
                          width: "40px",
                          borderRadius: "50%",
                        }}
                        src={`${baseUrl}/${comment?.author?.profileImage}`}
                        alt=""
                      />
                    </div>
                    <span className="comment-author">
                      {comment?.author?.userName}:
                    </span>
                    <span className="comment-text">{comment?.text}</span>
                  </div>
                  <div style={{ display: "flex" }}>
                    {token && comment?.author?._id === user?._id && (
                      <div className="comment-icons">
                        <DeleteIcon
                          className="icon delete-icon"
                          onClick={() =>
                            handleDeleteComment(post._id, comment._id)
                          }
                        />
                      </div>
                    )}

                    <div className="comment-icons">
                      <span>{comment?.likes?.length}</span>
                      <FavoriteIcon
                        className={`icon ${
                          isCommentLiked(comment?._id) ? "liked" : ""
                        }`}
                        onClick={() => handleCommentLike(comment?._id)}
                      />
                    </div>

                    {/* Reply Comment */}
                    <div className="comment-icons">
                      <ReplyIcon
                        className="icon reply-icon"
                        onClick={() =>
                          handleReplyComment(post._id, comment._id)
                        }
                      />
                    </div>
                  </div>
                </div>

                {/* Render Reply Comments */}
                <div>
                  {comment.replyComments &&
                    comment.replyComments.length > 0 && (
                      <div className="reply-comments">
                        {comment.replyComments.map((reply, replyIndex) => (
                          <div key={replyIndex} className="reply-comment">
                            <div className="comment-content">
                              <div
                                style={{
                                  display: "flex",
                                  justifyContent: "center",
                                  alignItems: "center",
                                  height: "40px",
                                  width: "40px",
                                  borderRadius: "50%",
                                  border: "1px solid black",
                                  marginRight: "5px",
                                }}
                              >
                                <img
                                  style={{
                                    height: "40px",
                                    width: "40px",
                                    borderRadius: "50%",
                                  }}
                                  src={`${baseUrl}/${reply?.author?.profileImage}`}
                                  alt=""
                                />
                              </div>
                              <span className="comment-author">
                                {reply?.author?.userName}:
                              </span>
                              <span className="comment-text">
                                {reply?.text}
                              </span>
                            </div>
                            <div style={{ display: "flex" }}>
                              {token && reply?.author?._id === user?._id && (
                                <div className="comment-icons">
                                  <DeleteIcon
                                    className="icon delete-icon"
                                    onClick={() =>
                                      handleDeleteReply(
                                        post._id,
                                        comment._id,
                                        reply._id
                                      )
                                    }
                                  />
                                </div>
                              )}
                              <div className="comment-icons">
                                <span>{reply?.likes?.length}</span>
                                <FavoriteIcon
                                  // className={`icon ${
                                  //   isReplyLiked(reply?._id) ? "liked" : ""
                                  // }`}
                                  onClick={() => handleReplyLike(reply?._id)}
                                />
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                    )}

                  {/* Render Reply Comment Form */}
                  {showReplyCommentField === comment._id && (
                    <form
                      onSubmit={(event) =>
                        handleReplySubmit(event, post._id, comment._id)
                      }
                      className="reply-comment-form"
                    >
                      <TextField
                        id="reply-comment-input"
                        label="Reply to comment"
                        variant="outlined"
                        value={replyComment}
                        onChange={(event) =>
                          setReplyComment(event.target.value)
                        }
                      />
                      <button type="submit" className="reply-comment-button">
                        Reply
                      </button>
                    </form>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
};

export default Post;
